.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-hello {
    animation: App-logo-spin infinite 5s ease-in;
    color: #027979;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(3);
  }
}
